.navbar {

    height: 6.5rem;
    justify-content: space-between;
    display: flex; }

.name__vet {
    color: white;
    font-family: 'Parisienne', cursive;
    font-family: 'Dancing Script', cursive;
    font-size: 1.5rem;
    margin-left: 1rem; }

.logo {
    width: 3.5rem;
    border-radius: 0.3rem;
    display: -webkit-inline-box; }

.information__vet {
    color: #e6e6e6;
    font-size: 1.3rem;
    text-align: center; }

.button_logReg {
    border: 0.1rem solid #242121;
    margin: 0.4rem;
    border-radius: 0.2rem;
    height: 2.3rem;
    background-color: #457197;
    color: white; }

.fa-paw {
    font-size: 1.4rem;
    color: #ffffff8a;
    margin: 0.5rem; }

.paw {
    background-color: #383535;
    border: 0.2rem solid #3f3c3c; }

.btn {
    height: 3.5rem;
    background-color: #201f1f;
    margin-right: 1rem;
    font-size: 1rem; }

.dropdown-menu {
    font-size: 1.5rem;
    margin-top: 3.5rem;
    background-color: #1c4771;
    border-radius: 0.6rem;
    height: 15rem;
    color: #d2d1d1; }
